    right: 2rem;
}

.mask-box tr .delBox {
    flex: 1
}

.demarcate-table tr .delBox {
    /* top:2px; */
    position: relative;
}

.demarcate-table tr .delBox .delOne {
    position: absolute;
    top: 21%;
    left: 0;
}
</style>