import axios_ins from "../../utils/axios_ins";


// 读传感器值
export function devCalibrateSensorKb() {
    return axios_ins({
        url: 'dev/calibrate/sensor/kb',
        method: "get",
    })
}
// 读设备值
export function devCalibrateSensorResult() {
    return axios_ins({
        url: 'dev/calibrate/sensor/result',
        method: "get",
    })
}
// 写传感器值
export function devCalibrateSensorSet(params) {
    return axios_ins({
        url: 'dev/calibrate/sensor/set',
        method: "post",
        data: params
    })
}

// 进入出厂标定
export function devCalibrateFacApi() {
    return axios_ins({
        url: 'dev/calibrate/fac',
        method: "get",
    })
}

// 进入工艺标定
export function devCalibrateProApi() {
    return axios_ins({
        url: 'dev/calibrate/pro',
        method: "get",
    })
}
// 退出标定
export function devCalibrateExitApi() {
    return axios_ins({
        url: 'dev/calibrate/exit',
        method: "get",
    })
}
//是否启用工艺标定
export function devCalibrateEnableApi() {
    return axios_ins({
        url: 'dev/calibrate/enable',
        method: "get",
    })
}


